@import 'variables';


.main {
    &__about {
        width: 100%;
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        &__block1 {
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
            background: url("../img/who_photo.png") no-repeat center;
            background-size: cover;
            max-width: 100%;
            min-width: 100%;
            min-height: calc( 100vh - 80px );
            max-height: calc( 100vh - 80px );
            // &__background
            //     max-width: 100vw
            //     min-width: 100vw
            //     min-height: calc( 100vh - 80px )
            //     max-height: calc( 100vh - 80px )
            &__h2 {
                font-family: 'decorative';
                font-size: 70px;
                color: #fff;
                position: absolute;
                text-align: center;
                // display: flex
                // justify-content: space-around
                // align-items: center
                // max-height: 500px
 } }                // min-height: 400px
        &__block2 {
            min-height: calc( 100vh - 80px );
            max-height: calc( 100vh - 80px );
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
            max-width: calc( 100vw - 100px );
            &__text {
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                &__h2 {
                    font-family: 'mainBold';
                    font-size: 48px;
                    text-transform: uppercase;
                    &__underline {
                        @include underline; } }
                &__p {
                    font-family: 'mainLight';
                    font-size: 24px;
                    line-height: 120%;
                    max-width: 410px; } } } } }
